<template>
	<view class="content">
		<u-navbar :is-back="true" title="项目详情" 
			title-color='#fff' 
		back-icon-color='#fff' 
		:background="{ background:'#213244'}">
		</u-navbar>
		<container class='container'>
			<ls-swiper 
			:list="base_lsit" 
			imgKey="imgUrl" 
			:loop="true" 
			:dots='true' 
			:autoplay='true' 
			:height='250' 
			:interval='5000'
			@clickItem="clickItem()" />
		</container>
		<view class="zhengti">
					<view class='title f8c'>
						<span>{{activity.name}}</span>
					</view>	
					<view class='beian'>
						<span>备案名{{activity.record}}</span>
					</view>
					<view class='jiage' >
						<span>价格:{{activity.price}}元</span>
					</view>
					<view >
						<span class=' ys'>开盘时间:</span><span>{{activity.opening}}</span>
					</view>
					<view >
						<span class=' ys'>电话:</span><span>{{activity.telephone}}</span>
					</view>
					<view >
						<span class=' ys'>地址:</span><span>{{activity.address}}</span>
					</view>
					<view class="gd" @click.stop="openinfo('/pages/index/more')">
						更多详情》
					</view>
					<view class='hxt'>
						<span>户型图</span>
					</view>
					<view v-for="(item,index) in hxt" :key="index" class="activity_content"   >
						<image mode='aspectFill' class='imgeh' :src='item.img' :lazy-load='true'>
						<view class=' '>
							<span class=''>{{item.title}}</span> 
							<span class=''>建面：{{item.architecture}}</span>
						</view>
					</view>
					<view class=' '>
						<span>置业顾问</span>
					</view>
					<view v-for="(item,index) in adviser" :key="index" class="activity_content flex-row start top"   >
						<image mode='aspectFill' class='' :src='item.logo' :lazy-load='true'>
						<view class=' flex-column' >
							<span class=''>{{item.username}}</span>
						</view>
						<button class='' size='mini' type="default" @click="makePhoneCall(item.telephone)">
							电话咨询
						</button>
					</view>
					<view class=''>
						<span>周边配套</span>
					</view>
		</view>
	</view>
</template>

<script>
	import api from '../../api/index.js'
	import container from '../../components/container/index.vue'
	import LsSwiper from '../../components/ls-swiper/index.vue'
	import uniIcons from "@/components/uni-icons/uni-icons.vue"
	import {
		AkxhHd
	} from '../../api/akxhhd.js'
	export default {
		components: {
			container,
			LsSwiper,
			uniIcons,
		},
		data() {
			return {
				base_lsit: [],//轮播图
				activity:[],//详情
				hxt:[],
				adviser:[],
			}
		},
		onLoad() {
			this.init();
		},
		methods: {
			init(){
				this.getBase();
				this.AkxhHdlists();
			},
			openinfo(url) {
			    uni.navigateTo({
			     url: url,
			    });
			},
			makePhoneCall(phone){
				uni.makePhoneCall({
				    phoneNumber: phone
				});
			},
			AkxhHdlists(){
				AkxhHd('/catalyzer/AkxhXq/list','GET').then(res => {
					if (res.data.code == 200) {
						let result = res.data.result;
						this.activity = result[0];
						this.activity.opening = result[0].opening.substring(0,10);
					}
				})
				AkxhHd('/catalyzer/AkxhHx/list','GET').then(res => {
					if (res.data.code == 200) {
						let result = res.data.result;
						let hxt = [];
						result.forEach((item,index,array)=>{
							item.img = this.imgURL + item.img;
							hxt.push(item);
						});
						this.hxt = hxt;
					}
				})
				AkxhHd('/catalyzer/AkxhGw/list','GET').then(res => {
					if (res.data.code == 200) {
						let result2 = res.data.result;
						let arr = [];
						result2.forEach((item2,index2,array2)=>{
							item2.logo = this.imgURL + item2.logo;
							arr.push(item2);
						});
						this.adviser = arr;
					}
				})
			},
			getBase() {
				api.base(100).then(res => {
					this.base_lsit = res
				})
			},
			clickItem(data) {
			},
			back() {
				uni.redirectTo({
					url: '/pages/index/index',
				})
			}
		}
	}
</script>

<style>
	.headers{
		height: 5vh;
		background: #1c2a39;
		background-image: radial-gradient(left, circle cover, #090e14 0%, #284159 100%);
		/* background-image: radial-gradient(top, circle cover, #090e14 0%, #284159 80%); */
		width: 100vw;
	}
	.content .zhengti{
		background-color: #1c2a39;
		color:#fff;	
	}
	.content .zhengti .title{
		color:#fff;	
	}
	.content .zhengti .beian{
		color:#a3a3a3;	
	}
	.content .zhengti .jiage{
		color:#ff0000;
		font-size: 20px;
	}
	.ys{
		color:#a3a3a3;
	}
	.content .zhengti .gd{
		width: 80%;
		height: 40PX;
		font-size: 20px;
		text-align: center;
		margin: 0 auto;
		border-radius: 5px;
		background-color: #a3a3a3;
	}
	.content .zhengti .hxt{
		color:#ffffff;
		font-size: 24px;
	}
	.imgeh{
		width: 45%;
		height: 100px;
	}
	
</style>
